'use client';

import { usePrivy } from '@privy-io/react-auth';
import RideList from './RideList';

interface MapSidebarProps {
  isOpen: boolean;
  onToggle: () => void;
  pickupCoords?: { lat: number; lng: number };
  dropoffCoords?: { lat: number; lng: number };
  onTopMatchUpdate?: (topMatch: any) => void;
}

export default function MapSidebar({ isOpen, onToggle, pickupCoords, dropoffCoords, onTopMatchUpdate }: MapSidebarProps) {
  const { authenticated } = usePrivy();

  if (!authenticated) return null;

  return (
    <>
      {/* Hamburger Toggle Button - Now controlled by parent or rendered here but triggering prop */}
      <button
        onClick={onToggle}
        className={`absolute top-4 left-4 z-60 p-2.5 bg-white rounded-full shadow-md text-gray-700 hover:bg-gray-50 transition-all duration-300 pointer-events-auto ${isOpen ? 'translate-x-[320px]' : 'translate-x-0'}`}
        aria-label="Toggle Sidebar"
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          {isOpen ? (
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          ) : (
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
          )}
        </svg>
      </button>

      {/* Sidebar Overlay (closes when clicking background) */}
      {isOpen && (
        <div 
          className="fixed inset-0 bg-black/20 z-55 backdrop-blur-[1px] transition-opacity"
          onClick={onToggle}
        />
      )}

      {/* Sidebar Panel */}
      <div 
        className={`fixed top-0 left-0 bottom-0 w-[320px] bg-white shadow-2xl z-58 flex flex-col transition-transform duration-300 ease-in-out ${
          isOpen ? 'translate-x-0' : '-translate-x-full'
        }`}
      >
        {/* Header Branding */}
        <div className="p-5 border-b border-gray-100 bg-white shrink-0 pt-8">
            <h2 className="text-2xl font-extrabold text-gray-900 tracking-tight">Pincher</h2>
            <p className="text-xs text-gray-500 font-medium">Smart Ride Sharing</p>
        </div>

        {/* Ride List Content */}
        <div className="flex-1 overflow-y-auto bg-gray-50/50">
          <div className="p-4">
            <RideList pickupCoords={pickupCoords} dropoffCoords={dropoffCoords} onTopMatchUpdate={onTopMatchUpdate} />
          </div>
        </div>
        
        {/* Footer */}
        <div className="p-3 border-t border-gray-100 text-center text-xs text-gray-400 bg-white">
          Pincher ¬© 2025
        </div>
      </div>
    </>
  );
}
