'use client';

import { useState, useEffect, useRef } from 'react';
import { usePrivy } from '@privy-io/react-auth';
import { supabase } from '@/lib/supabase';

interface SearchBoxProps {
  onPickupChange?: (location: string, coords?: { lat: number; lng: number }) => void;
  onDropoffChange?: (location: string, coords?: { lat: number; lng: number }) => void;
  className?: string;
  externalPickupCoords?: { lat: number; lng: number };
  externalDropoffCoords?: { lat: number; lng: number };
}

export default function SearchBox({ 
  onPickupChange, 
  onDropoffChange, 
  className = "",
  externalPickupCoords,
  externalDropoffCoords
}: SearchBoxProps) {
  const { user, authenticated, login } = usePrivy();
  const [pickup, setPickup] = useState('');
  const [dropoff, setDropoff] = useState('');
  const [pickupCoords, setPickupCoords] = useState<{ lat: number; lng: number } | null>(null);
  const [dropoffCoords, setDropoffCoords] = useState<{ lat: number; lng: number } | null>(null);
  
  // ... (previous states)

  // Sync with External Coordinates (e.g. Map Drag)
  useEffect(() => {
    if (externalPickupCoords && 
       (!pickupCoords || externalPickupCoords.lat !== pickupCoords.lat || externalPickupCoords.lng !== pickupCoords.lng)) {
        
        // Update local coords immediately
        setPickupCoords(externalPickupCoords);
        
        // Reverse Geocode to get address text
        if (window.google && window.google.maps) {
            const geocoder = new google.maps.Geocoder();
            geocoder.geocode({ location: externalPickupCoords }).then((response) => {
                if (response.results[0]) {
                    setPickup(response.results[0].formatted_address);
                }
            }).catch((e) => console.error("Reverse geocode failed", e));
        }
    }
  }, [externalPickupCoords]);

  useEffect(() => {
    if (externalDropoffCoords && 
       (!dropoffCoords || externalDropoffCoords.lat !== dropoffCoords.lat || externalDropoffCoords.lng !== dropoffCoords.lng)) {
        
        setDropoffCoords(externalDropoffCoords);
        
        if (window.google && window.google.maps) {
            const geocoder = new google.maps.Geocoder();
            geocoder.geocode({ location: externalDropoffCoords }).then((response) => {
                if (response.results[0]) {
                    setDropoff(response.results[0].formatted_address);
                }
            }).catch((e) => console.error("Reverse geocode failed", e));
        }
    }
  }, [externalDropoffCoords]);

  // Rest of the existing Effects...
  const [wechatId, setWechatId] = useState('');
  const [departureTime, setDepartureTime] = useState('');
  const [loading, setLoading] = useState(false);
  const [locating, setLocating] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  const [placesApiReady, setPlacesApiReady] = useState(false);

  const pickupInputRef = useRef<HTMLInputElement>(null);
  const dropoffInputRef = useRef<HTMLInputElement>(null);
  const pickupAutocompleteRef = useRef<google.maps.places.Autocomplete | null>(null);
  const dropoffAutocompleteRef = useRef<google.maps.places.Autocomplete | null>(null);

  // Check if Places API is available
  useEffect(() => {
    const checkPlacesAPI = () => {
      if (window.google && window.google.maps && window.google.maps.places) {
        setPlacesApiReady(true);
        return true;
      }
      return false;
    };

    if (checkPlacesAPI()) {
      return;
    }

    const interval = setInterval(() => {
      if (checkPlacesAPI()) {
        clearInterval(interval);
      }
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  // Initialize Google Places Autocomplete
  useEffect(() => {
    if (!placesApiReady || !window.google || !window.google.maps || !window.google.maps.places) {
      return;
    }

    // Setup pickup autocomplete
    if (pickupInputRef.current && !pickupAutocompleteRef.current) {
      pickupAutocompleteRef.current = new google.maps.places.Autocomplete(
        pickupInputRef.current,
        {
          componentRestrictions: { country: 'us' },
          fields: ['formatted_address', 'geometry', 'name'],
          types: ['establishment', 'geocode'],
        }
      );

      pickupAutocompleteRef.current.addListener('place_changed', () => {
        const place = pickupAutocompleteRef.current?.getPlace();
        if (place && place.geometry && place.geometry.location) {
          const address = place.formatted_address || place.name || '';
          const coords = {
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng(),
          };
          setPickup(address);
          setPickupCoords(coords);
          if (onPickupChange) {
            onPickupChange(address, coords);
          }
        }
      });
    }

    // Setup dropoff autocomplete
    if (dropoffInputRef.current && !dropoffAutocompleteRef.current) {
      dropoffAutocompleteRef.current = new google.maps.places.Autocomplete(
        dropoffInputRef.current,
        {
          componentRestrictions: { country: 'us' },
          fields: ['formatted_address', 'geometry', 'name'],
          types: ['establishment', 'geocode'],
        }
      );

      dropoffAutocompleteRef.current.addListener('place_changed', () => {
        const place = dropoffAutocompleteRef.current?.getPlace();
        if (place && place.geometry && place.geometry.location) {
          const address = place.formatted_address || place.name || '';
          const coords = {
            lat: place.geometry.location.lat(),
            lng: place.geometry.location.lng(),
          };
          setDropoff(address);
          setDropoffCoords(coords);
          if (onDropoffChange) {
            onDropoffChange(address, coords);
          }
          // Auto show details when both locations are selected
          if (pickup) {
            setShowDetails(true);
          }
        }
      });
    }
  }, [placesApiReady, onPickupChange, onDropoffChange, pickup]);

  // Geolocation
  const handleGetCurrentLocation = async () => {
    if (!navigator.geolocation) {
      alert('Âú∞ÁêÜÂÆö‰Ωç‰∏çÊîØÊåÅ / Geolocation not supported');
      return;
    }

    if (!window.google || !window.google.maps) {
      alert('Âú∞ÂúñÂä†Ëºâ‰∏≠ÔºåË´ãÁ®çÂæåÂÜçË©¶ / Map is loading, please try again');
      return;
    }

    setLocating(true);
    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;

        try {
          if (window.google && window.google.maps) {
            const geocoder = new google.maps.Geocoder();
            const result = await geocoder.geocode({
              location: { lat: latitude, lng: longitude },
            });

            if (result.results && result.results.length > 0) {
              const address = result.results[0].formatted_address;
              const coords = { lat: latitude, lng: longitude };
              setPickup(address);
              setPickupCoords(coords);
              if (onPickupChange) {
                onPickupChange(address, coords);
              }
            }
          }
        } catch (error) {
          console.error('Geocoding error:', error);
        } finally {
          setLocating(false);
        }
      },
      (error) => {
        console.error('Geolocation error:', error);
        alert('Êó†Ê≥ïËé∑Âèñ‰ΩçÁΩÆ / Unable to get location');
        setLocating(false);
      }
    );
  };

  const handleSubmit = async () => {
    if (!authenticated) {
      login();
      return;
    }

    if (!pickup || !dropoff || !wechatId) {
      alert('ËØ∑Â°´ÂÜôÊâÄÊúâÂøÖÂ°´Â≠óÊÆµ / Please fill in all required fields');
      return;
    }

    setLoading(true);
    try {
      const { error } = await supabase
        .from('rides')
        .insert([
          {
            user_address: user?.wallet?.address || user?.email?.address || 'unknown',
            pickup_location: pickup,
            dropoff_location: dropoff,
            departure_time: departureTime || new Date().toISOString(),
            wechat_id: wechatId,
            status: 'active',
            pickup_lat: pickupCoords?.lat,
            pickup_lng: pickupCoords?.lng,
            dropoff_lat: dropoffCoords?.lat,
            dropoff_lng: dropoffCoords?.lng,
          },
        ])
        .select()
        .single();

      if (error) {
        console.error('Supabase error:', error);
        alert(`Failed to create ride: ${error.message}`);
        throw error;
      }

      alert('Ë°åÁ®ãÂ∑≤ÂèëÂ∏ÉÔºÅAI Ê≠£Âú®ÂØªÊâæÊãºÂèã... / Ride published!');

      // Reset
      setPickup('');
      setDropoff('');
      setPickupCoords(null);
      setDropoffCoords(null);
      setWechatId('');
      setDepartureTime('');
      setShowDetails(false);
    } catch (error: any) {
      console.error('Error creating ride:', error);
    } finally {
      setLoading(false);
    }
  };

  // Manual geocode when Enter is pressed
  const handleManualGeocode = async (address: string, isPickup: boolean) => {
    if (!address || !window.google || !window.google.maps) return;

    try {
      const geocoder = new google.maps.Geocoder();
      const result = await geocoder.geocode({ address });

      if (result.results && result.results.length > 0) {
        const location = result.results[0].geometry.location;
        const formattedAddress = result.results[0].formatted_address;
        const coords = { lat: location.lat(), lng: location.lng() };

        if (isPickup) {
          setPickup(formattedAddress);
          setPickupCoords(coords);
          if (onPickupChange) {
            onPickupChange(formattedAddress, coords);
          }
        } else {
          setDropoff(formattedAddress);
          setDropoffCoords(coords);
          if (onDropoffChange) {
            onDropoffChange(formattedAddress, coords);
          }
          if (pickup) {
            setShowDetails(true);
          }
        }
      }
    } catch (error) {
      console.error('Geocoding error:', error);
    }
  };

  const handleSwap = () => {
    const tempPickup = pickup;
    const tempPickupCoords = pickupCoords;

    setPickup(dropoff);
    setPickupCoords(dropoffCoords);
    if (onPickupChange) onPickupChange(dropoff, dropoffCoords || undefined);

    setDropoff(tempPickup);
    setDropoffCoords(tempPickupCoords);
    if (onDropoffChange) onDropoffChange(tempPickup, tempPickupCoords || undefined);
  };

  return (
    <>
      {/* Google Maps Style Search Box */}
      <div className={`absolute top-4 left-16 z-50 w-[92%] max-w-[400px] transition-transform duration-300 ease-in-out ${className}`}>
        {/* Places API Status Warning */}
        {!placesApiReady && (
          <div className="mb-3 bg-yellow-50 border border-yellow-200 rounded-lg p-3 shadow-sm">
            <div className="flex items-center gap-2">
              <svg className="w-5 h-5 text-yellow-600 shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <p className="text-sm text-yellow-800">
                <strong>Ëá™ÂãïË£úÂÖ®Â∞öÊú™Â∞±Á∑í„ÄÇ</strong> Ë´ãËº∏ÂÖ•ÂÆåÊï¥Âú∞ÂùÄÂæåÊåâ EnterÔºåÊàñ‰ΩøÁî®ÂÆö‰ΩçÊåâÈàï üìç
              </p>
            </div>
          </div>
        )}

        <div className="bg-white rounded-2xl shadow-[0_6px_16px_rgba(0,0,0,0.12)] overflow-hidden">
          {/* Location Inputs Area */}
          <div className="relative p-0">
            {/* Connector Visualization */}
            <div className="absolute left-[26px] top-[26px] bottom-[26px] flex flex-col items-center pointer-events-none z-10 w-4">
              {/* Pickup Dot */}
              <div className="w-3 h-3 rounded-full border-[3px] border-black bg-white box-border shrink-0" />
              {/* Vertical Line */}
              <div className="flex-1 w-[2px] bg-linear-to-b from-gray-300 to-gray-400 my-1 rounded-full opacity-50" />
              {/* Dropoff Square */}
              <div className="w-3 h-3 bg-black rounded-[1px] shrink-0" />
            </div>

            {/* Inputs Container */}
            <div className="flex flex-col">
              {/* Pickup Input */}
              <div className="relative flex items-center pr-12">
                 <input
                  ref={pickupInputRef}
                  className="w-full pl-14 pr-4 py-4 text-[15px] font-medium text-[#202124] placeholder-gray-500 bg-transparent focus:outline-none focus:bg-gray-50/50 transition-colors"
                  placeholder={placesApiReady ? "Ëµ∑Èªû" : "Ëº∏ÂÖ•Ëµ∑Èªû"}
                  value={pickup}
                  onChange={(e) => {
                    const val = e.target.value;
                    setPickup(val);
                    if (val === '' && onPickupChange) {
                        setPickupCoords(null);
                        onPickupChange('', undefined);
                    }
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !placesApiReady) {
                      handleManualGeocode(pickup, true);
                    }
                  }}
                />
                <button
                    onClick={handleGetCurrentLocation}
                    disabled={locating}
                    className="absolute right-3 p-2 text-gray-400 hover:text-black hover:bg-gray-100 rounded-full transition-all"
                    title="ÂÆö‰Ωç"
                  >
                  {locating ? (
                    <svg className="animate-spin h-5 w-5" fill="none" viewBox="0 0 24 24">
                      <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                      <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                    </svg>
                  ) : (
                    <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <title>‰ΩøÁî®ÁèæÂú®‰ΩçÁΩÆ</title>
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                    </svg>
                  )}
                </button>
              </div>

              {/* Divider */}
              <div className="ml-14 border-t border-gray-100" />

              {/* Dropoff Input */}
              <div className="relative flex items-center pr-12">
                 <input
                  ref={dropoffInputRef}
                  className="w-full pl-14 pr-4 py-4 text-[15px] font-medium text-[#202124] placeholder-gray-500 bg-transparent focus:outline-none focus:bg-gray-50/50 transition-colors"
                  placeholder={placesApiReady ? "ÁõÆÁöÑÂú∞" : "Ëº∏ÂÖ•ÁõÆÁöÑÂú∞"}
                  value={dropoff}
                  onChange={(e) => {
                    const val = e.target.value;
                    setDropoff(val);
                    if (val === '' && onDropoffChange) {
                        setDropoffCoords(null);
                        onDropoffChange('', undefined);
                    }
                  }}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !placesApiReady) {
                      handleManualGeocode(dropoff, false);
                    }
                  }}
                />
                 {/* Swap Button (Floating right overlap) - Removed circle bg */}
                  <div className="absolute right-3 -top-[23px] z-20">
                    <button
                      onClick={handleSwap}
                      className="p-1.5 text-gray-400 hover:text-black transition-colors"
                      title="ÂàáÊèõËµ∑ÁµÇÈªû"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16V4m0 0L3 8m4-4l4 4m6 0v12m0 0l4-4m-4 4l-4-4" />
                      </svg>
                    </button>
                  </div>
              </div>
            </div>
          </div>

          {/* Details Section (collapsed by default) */}
          {showDetails && (
            <div className="border-t border-gray-100 p-4 bg-white animate-in slide-in-from-top-2 duration-200">
              <div className="space-y-4">
                {/* Departure Time */}
                <div>
                  <label className="block text-xs font-bold text-gray-500 uppercase tracking-wider mb-1.5 ml-1">
                    Âá∫ÁôºÊôÇÈñì
                  </label>
                  <input
                    type="datetime-local"
                    className="w-full px-4 py-3 bg-gray-50 border-0 rounded-xl text-gray-900 font-medium focus:ring-2 focus:ring-blue-500/20 focus:bg-white transition-all"
                    value={departureTime}
                    onChange={(e) => setDepartureTime(e.target.value)}
                  />
                </div>

                {/* WeChat ID */}
                <div>
                   <label className="block text-xs font-bold text-gray-500 uppercase tracking-wider mb-1.5 ml-1">
                    ÂæÆ‰ø°Ëôü <span className="text-red-500">*</span>
                  </label>
                  <input
                    className="w-full px-4 py-3 bg-gray-50 border-0 rounded-xl text-gray-900 font-medium focus:ring-2 focus:ring-blue-500/20 focus:bg-white transition-all placeholder:font-normal"
                    placeholder="Ëº∏ÂÖ•‰Ω†ÁöÑÂæÆ‰ø°Ëôü"
                    value={wechatId}
                    onChange={(e) => setWechatId(e.target.value)}
                  />
                </div>

                {/* Submit Button */}
                <button
                  onClick={handleSubmit}
                  disabled={loading || !wechatId}
                  className="w-full py-3.5 bg-black text-white rounded-xl font-bold text-[15px] hover:bg-gray-800 disabled:bg-gray-200 disabled:text-gray-400 disabled:cursor-not-allowed transition-all shadow-lg shadow-gray-200/50"
                >
                  {loading ? (
                    <span className="flex items-center justify-center gap-2">
                       <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                      </svg>
                      ÁôºÂ∏É‰∏≠...
                    </span>
                  ) : authenticated ? 'Á¢∫Ë™çÁôºÂ∏ÉË°åÁ®ã' : 'ÁôªÂÖ•‰∏¶ÁôºÂ∏É'}
                </button>
              </div>
            </div>
          )}

          {/* Toggle Details Button */}
          {!showDetails && pickup && dropoff && (
            <div className="border-t border-gray-100">
              <button
                onClick={() => setShowDetails(true)}
                className="w-full py-3 text-[#1a73e8] font-bold text-sm hover:bg-blue-50/50 transition-colors flex items-center justify-center gap-1"
              >
                ‰∏ã‰∏ÄÊ≠•
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M19 9l-7 7-7-7" />
                 </svg>
              </button>
            </div>
          )}
        </div>
      </div>
    </>
  );
}
