'use client';

import { usePrivy } from '@privy-io/react-auth';
import { useState, useEffect } from 'react';
import { createPublicClient, http, formatUnits, erc20Abi } from 'viem';
import { baseSepolia } from 'viem/chains';

const USDC_ADDRESS = '0x036CbD53842c5426634e7929541eC2318f3dCF7e';

export default function UserProfile() {
  const { user, logout, authenticated } = usePrivy();
  const [showDropdown, setShowDropdown] = useState(false);
  const [funding, setFunding] = useState(false);
  const [balance, setBalance] = useState<string>('0.00');
  const [ethBalance, setEthBalance] = useState<string>('0.00');

  useEffect(() => {
    if (authenticated && user?.wallet?.address) {
        fetchBalance();
    }
  }, [authenticated, user?.wallet?.address, showDropdown]);

  const fetchBalance = async () => {
    try {
        const client = createPublicClient({
            chain: baseSepolia,
            transport: http(),
        });

        // Fetch USDC Balance
        console.log("Fetching balance for:", user!.wallet!.address);
        
        // @ts-expect-error - viem type inference issue
        const usdcData = await client.readContract({
            address: USDC_ADDRESS,
            abi: erc20Abi,
            functionName: 'balanceOf',
            args: [user!.wallet!.address as `0x${string}`],
        });
        console.log("Raw USDC Data:", usdcData);
        
        const usdcFormatted = formatUnits(usdcData as bigint, 6);
        console.log("Formatted USDC:", usdcFormatted);
        
        setBalance(parseFloat(usdcFormatted).toFixed(2));

        // Fetch ETH Balance
        const ethData = await client.getBalance({ 
            address: user!.wallet!.address as `0x${string}` 
        });
        const ethFormatted = formatUnits(ethData, 18);
        setEthBalance(parseFloat(ethFormatted).toFixed(4));

    } catch (e) {
        console.error("Balance fetch error", e);
    }
  };

  if (!authenticated) return null;

  const displayName = user?.email?.address || 
    (user?.wallet?.address ? `${user.wallet.address.slice(0, 6)}...${user.wallet.address.slice(-4)}` : 'User');

  const handleFundWallet = async () => {
    if (!user?.wallet?.address) return;
    setFunding(true);
    try {
      const res = await fetch('/api/faucet', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ address: user.wallet.address }),
      });
      const data = await res.json();
      if (data.success) {
        alert('Funding successful! Check your wallet on Base Sepolia.');
        fetchBalance(); // Refresh
      } else {
        alert('Funding failed: ' + (data.error || 'Unknown error'));
      }
    } catch (e) {
      alert('Funding error');
    } finally {
      setFunding(false);
    }
  };

  return (
    <div className="absolute top-4 right-4 z-50 font-sans">
      <div className="relative">
        <button
          onClick={() => setShowDropdown(!showDropdown)}
          className="flex items-center gap-3 bg-white px-4 py-2.5 rounded-full shadow-md hover:shadow-lg transition-shadow border border-gray-100"
        >
          <div className="w-8 h-8 rounded-full bg-linear-to-br from-blue-500 to-purple-600 flex items-center justify-center text-white font-bold text-xs">
            {displayName[0].toUpperCase()}
          </div>
          <span className="text-sm font-medium text-gray-700 hidden sm:block">
            {displayName}
          </span>
          <svg className={`w-4 h-4 text-gray-400 transition-transform ${showDropdown ? 'rotate-180' : ''}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </button>

        {showDropdown && (
          <div className="absolute right-0 mt-2 w-64 bg-white rounded-xl shadow-xl border border-gray-100 overflow-hidden py-1 animate-in fade-in slide-in-from-top-2 duration-200">
            <div className="px-4 py-3 border-b border-gray-50 bg-gray-50/50">
              <p className="text-xs text-gray-500 uppercase tracking-wider font-semibold">Signed in as</p>
              <div className="flex flex-col gap-1">
                <div className="flex items-center gap-2 bg-gray-50 p-2 rounded border border-gray-100">
                    <p className="text-[10px] font-mono text-gray-500 break-all leading-tight select-all">
                        {user?.wallet?.address}
                    </p>
                    <button 
                        onClick={() => {
                            if (user?.wallet?.address) {
                                navigator.clipboard.writeText(user.wallet.address);
                                alert('Address copied!');
                            }
                        }}
                        className="text-gray-400 hover:text-blue-600 shrink-0"
                        title="Copy Address"
                    >
                        <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" /></svg>
                    </button>
                    {user?.wallet?.address && (
                        <a 
                            href={`https://sepolia.basescan.org/address/${user.wallet.address}`}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="text-blue-500 hover:text-blue-700 shrink-0"
                            title="View on Explorer"
                        >
                            <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14" /></svg>
                        </a>
                    )}
                </div>
              </div>
              
              <div className="mt-3 flex flex-col gap-2">
                  <div className="flex items-center justify-between bg-white border border-gray-200 rounded-md px-2 py-1.5">
                      <div className="flex items-center gap-2">
                        <span className="text-sm">üí∞</span>
                        <span className="text-xs font-bold text-gray-800">{balance} USDC</span>
                      </div>
                  </div>
                  <div className="flex items-center justify-between bg-white border border-gray-200 rounded-md px-2 py-1.5">
                      <div className="flex items-center gap-2">
                        <span className="text-sm">‚õΩÔ∏è</span>
                        <span className="text-xs font-bold text-gray-800">{ethBalance} ETH</span>
                      </div>
                       <button onClick={fetchBalance} className="text-gray-400 hover:text-blue-600" title="Refresh">
                        <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" /></svg>
                      </button>
                  </div>
              </div>
            </div>
            
            {/* Faucet Links */}
            <div className="p-2 border-b border-gray-50 bg-blue-50/30">
                <p className="text-[10px] font-bold text-blue-600 px-2 uppercase tracking-wider mb-1">Get Testnet Funds</p>
                <a
                    href="https://portal.cdp.coinbase.com/products/faucet"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="px-2 py-1.5 text-xs text-gray-700 hover:bg-blue-100 rounded transition-colors flex items-center justify-between"
                >
                    <span>Base ETH ‚õΩÔ∏è</span>
                    <span className="text-gray-400">‚Üó</span>
                </a>
                 <a
                    href="https://faucet.circle.com/"
                    target="_blank"
                    rel="noopener noreferrer"
                    className="px-2 py-1.5 text-xs text-gray-700 hover:bg-blue-100 rounded transition-colors flex items-center justify-between"
                >
                    <span>USDC Token üí∞</span>
                    <span className="text-gray-400">‚Üó</span>
                </a>
            </div>

            <button
              onClick={logout}
              className="w-full text-left px-4 py-2.5 text-sm text-red-600 hover:bg-red-50 transition-colors flex items-center gap-2"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                 <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
              </svg>
              Sign Out
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
